/**
 *   Name: Bizagi Notifications
 *   Author: UI Forms Team
 *   Comments:
 *   -   This widget will implement a notifications box for the current page
 *
 *   Markup: <div></div>
 *   Must be appended to body
 *
 *   Styles:
 *       -   bz-form-notification
 *       -   bz-form-notification__content
 *       -   bz-form-notification__header
 *       -   bz-form-notification__item
 */

(function ($) {
    $.widget("ui.bizagi_notifications", {
        options: {
            containerAdditionalClass: "",
            headerAdditionalClass: "",
            contentAdditionalClass: "",
            itemAdditionalClass: "",
            itemIcon: "ui-icon-alert",
            title: "Notifications",
            minimized: true,
            clearEnabled: true,
            location: "bottom",
            device: bizagi.detectDevice() || "desktop"
        },

        /**
         * Constructor
         * @private
         */
        _init: function () {
            var self = this;
            var options = self.options;
            var container = self.element;

            self.counter = 0;

            // Creates container
            container.addClass(options.containerAdditionalClass)
                .addClass("ui-bizagi-notifications-container") // TODO: Remove selector of  function "clearValidationMessages"
                .addClass("bz-form-notification");

            if (options.orientation === "rtl") {
                container.addClass("bz-form-notification--reverse");
            }

            // Set collapsed  / expanded class
            container.addClass(options.minimized ? "bz-form-notification--collapsed" : "bz-form-notification--expanded");

            // Creates content div
            var content = $("<div class='bz-form-notification__content " + options.contentAdditionalClass + "'></div>");

            // Creates header
            var header = $("<div class='bz-form-notification__header " + options.headerAdditionalClass + "'></div>");
            header.data("expanded", !options.minimized).html("<label>" + options.title + "</label>");

            // Add / Remove hover class
            header.mouseenter(function () {
                container.addClass("bz-form-notification--hover");
            });
            header.mouseleave(function () {
                container.removeClass("bz-form-notification--hover");
            });
            header.click(function () {
                self.hide();
            });

            // Add Header button
            self.headerButton = $("<button class='bz-btn-icn bz-btn-icn--24'>" +
                "    <svg class='bz-icn' width='16px' height='16px'>" +
                "        <use xlink:href='#icon-cancel'></use>" +
                "    </svg>" +
                "</button>")
                .appendTo(header);

            // Arrange elements
            container.append(header).append(content);

            if (options.minimized)
                content.hide();

            // Keep references
            self.content = content;
            self.header = header;
        },

        /**
         * Add a validation message
         * @param message
         * @param data
         * @param icon
         * @param itemAdditionalClass
         * @param clickCallback
         */
        addNotification: function (message, data, icon, itemAdditionalClass, clickCallback, type, renderId) {
            if (message && message.replace) {
                var self = this;
                var options = self.options;
                var container = self.element;
                var content = self.batchMode ? self.batchContent : self.content;

                icon = icon || options.itemIcon;
                itemAdditionalClass = itemAdditionalClass || options.itemAdditionalClass;

                if (typeof message != "string") message = message.toString();

                message = bizagi.util.encodeHtml(message.replace(/<(\/?(?:STRONG|FONT))>/gi, "#$1!#"));
                message = message.replace(/#(\/?(?:STRONG|FONT))!#/gi, "<$1>");
                message = message.replace(/\n|\r/g, "<br/>");

                var item = $("<div class='bz-form-notification__item " + itemAdditionalClass + "'>" +
                    "<label>" + message + "</label>" +
                    "</div>").appendTo(content);

                // Scroll to this control
                if (!self.batchMode && self._hasScrollBar()) {
                    item[0].scrollIntoView();
                }

                // Bind click event
                item.bind("click", function () {
                    var dataForm = {
                        focus: data || null,
                        id: renderId || null
                    };

                    // Trigger event
                    container.triggerHandler("itemClick", {data: dataForm});
                    if (clickCallback) clickCallback({data: data});
                });

                if (!self.batchMode) {
                    self.show();
                }

                self.counter++;
            }
        },

        /**
         * Clear all messages
         * @param hide
         */
        clearAll: function (hide) {
            hide = hide === undefined ? true : hide;

            // Clear all data
            bizagi.util.removeItemLocalStorage("bizagi.mobility.trace");

            this.content.empty();
            this.counter = 0;

            if (hide) {
                this.hide();
            } else {
                this.show();
            }
        },

        /**
         * Show the notifications box
         */
        show: function () {
            this.element.css("z-index", $.getMaxZindex() + 1);

            // Show container
            if (!this.isContentVisible())
                this.element.fadeIn();
        },

        /**
         * Hide the notifications box
         */
        hide: function () {
            // Hide container
            if (this.isContentVisible())
                this.element.hide();
        },

        /**
         * Returns the number of notifications
         * @returns {jQuery|*}
         */
        count: function () {
            // Hide container
            return this.content.children().size();
        },

        /**
         * Check if the content has scroll bars or not
         * @returns {boolean}
         * @private
         */
        _hasScrollBar: function () {
            // If container is not visible return false
            if (!this.isContentVisible()) return false;

            return bizagi.util.hasScroll(this.content, "vertical");
        },

        /**
         * Toggles container view
         */
        onHeaderClick: function () {
            if (this.isExpanded()) {
                this.collapse();
            } else {
                this.expand();
            }
        },

        /**
         * Method to check if the container is expanded or not
         * @returns {boolean}
         */
        isExpanded: function () {
            return this.header.data("expanded") == true;
        },

        /**
         * Collapses the container
         */
        collapse: function () {
            var container = this.element;
            var header = this.header;

            // Collapse
            header.data("expanded", false);

            container.removeClass("bz-form-notification--expanded");
            container.addClass("bz-form-notification--collapsed");
            this.content.hide();
        },

        /**
         * Expand the container
         */
        expand: function () {
            var container = this.element;
            var header = this.header;

            // Expand
            header.data("expanded", true);

            container.removeClass("bz-form-notification--collapsed");
            container.addClass("bz-form-notification--expanded");
            this.content.show();
        },

        /**
         * Auto select first error elements
         */
        autoFocusFirstError: function () {
        },

        /**
         * Starts the batch mode
         */
        initBatch: function () {
            this.batchMode = true;
            this.batchContent = $("<div></div>");
        },

        /**
         * Ends the batch mode
         */
        endBatch: function () {
            this.batchMode = false;

            // Perform batch
            this.batchContent.children().appendTo(this.content);
            this.batchContent.detach();
            this.batchContent = null;

            if (this.content.children().length > 0)
                this.show();
        },

        /**
         * Check if content is visible
         * @returns {boolean}
         */
        isContentVisible: function () {
            var content = this.content;
            var element = this.element;

            return (element.length > 0 && element.css("display") !== "none") &&
                (content.length > 0 && content.css("display") !== "none");
        }
    });
})(jQuery);