/*
*   Name: Bizagi Rendering Services
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This class will provide a facade to access to workportal REST services
*/

bizagi.render.services.service.extend("bizagi.render.services.service", {},
{
    /* 
     *   Constructor
     */
    init: function(params) {
        // Call base
        var self = this;

        self._super(params);
        self.database = params.database;
    },

    getFormData: function(params) {
        var self = this;

        params = params || {};

        if (!params.isOfflineForm || bizagi.util.isOfflineFormsV2Supported()) {
            return self._super(params);
        } else {

            if (bizagi.util.isNativePluginSupported() && typeof(bizagiapp.getNativeFormData) !== "undefined") {

                return self.getFormDataFromNative(params)
                    .then(function(result) {
                        return self.database.mergeForm(result[1], result[0].data);
                    });

            } else {

                return $.when(self.database.getCase(params.idCase))
                    .then(function(response) {
                        var idWfClass = $.trim(response.idWfClass.toString());

                        return self.database.mergeForm(response, self.database.getFormData(idWfClass));
                    });
            }
        }
    },

    getFormDataFromNative: function(params) {
        var def = new $.Deferred();

        bizagiapp.getNativeFormData(params, function(form) {

            bizagiapp.getCase(params.idCase, function(caseInfo) {

                var result = [JSON.parse(form), JSON.parse(caseInfo)];

                def.resolve(result);

            }, function(error) {

                def.reject(error);

            });


        }, function(error) {
            def.reject(error);
        })

        return def.promise();
    },

    submitData: function(params) {
        var self = this;

        params = params || {};

        if (!params.isOfflineForm || bizagi.util.isOfflineFormsV2Supported()) {
            return self._super(params);
        } else {

            var data = self.resolveData(params.data || {}, params.xpathContext);
            data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + "action"] = params.action;

            if (bizagi.util.isNativePluginSupported() && typeof (bizagiapp.getNativeFormData) !== "undefined") {
                return self.saveNativeFormInfo(params, data);
            }

            return self.database.saveFormInfo(params, data);
        }
    },

    saveNativeFormInfo: function(params, data) {
        var self = this;

        var def = new $.Deferred();

        self.getFormDataFromNative(params).then(function(result) {
            return self.database.nativeSaveFormInfo(data, result[1], result[0].data)
                .done(function(doc) {
                    bizagiapp.saveFormData(params, doc);
                    def.resolve(doc);
                });
        });

        return def.promise();
    }
});
